	module.exports = {"data":{"site":{"siteMetadata":{"docSections":{"basics":["/basics/introduction/"],"guides":["/guides/quick-start-guide/","/guides/create-component-guide/"],"configurations":["/configurations/details/","/configurations/custom-webpack-config/","/configurations/preset-webpack/"],"components":["/components/overview/","/components/globals/","/components/wingsuit/","/components/twig/","/components/presentation/","/components/assets/","/components/javascript/"],"testing":["/testing/manual-testing/"]}}},"allMarkdownRemark":{"edges":[{"node":{"fields":{"slug":"/components/assets/"},"frontmatter":{"title":"Assets","id":"assets"}}},{"node":{"fields":{"slug":"/basics/introduction/"},"frontmatter":{"title":"Introduction","id":"introduction"}}},{"node":{"fields":{"slug":"/404/"},"frontmatter":{"title":"","id":null}}},{"node":{"fields":{"slug":"/components/globals/"},"frontmatter":{"title":"Global variables","id":"globals"}}},{"node":{"fields":{"slug":"/components/overview/"},"frontmatter":{"title":"Overview","id":"overview"}}},{"node":{"fields":{"slug":"/components/javascript/"},"frontmatter":{"title":"Javascript","id":"javascript"}}},{"node":{"fields":{"slug":"/components/twig/"},"frontmatter":{"title":"Twig only","id":"twig"}}},{"node":{"fields":{"slug":"/components/presentation/"},"frontmatter":{"title":"Presentation","id":"presentation"}}},{"node":{"fields":{"slug":"/configurations/custom-webpack-config/"},"frontmatter":{"title":"Custom webpack configuration","id":"custom-webpack-config"}}},{"node":{"fields":{"slug":"/components/wingsuit/"},"frontmatter":{"title":"Wingsuit (UI Pattern)","id":"wingsuit"}}},{"node":{"fields":{"slug":"/guides/create-component-guide/"},"frontmatter":{"title":"Create new component","id":"create-component-guide"}}},{"node":{"fields":{"slug":"/configurations/preset-webpack/"},"frontmatter":{"title":"Presets","id":"preset-webpack"}}},{"node":{"fields":{"slug":"/configurations/details/"},"frontmatter":{"title":"Configuration","id":"details"}}},{"node":{"fields":{"slug":"/guides/quick-start-guide/"},"frontmatter":{"title":"Quickstart guide","id":"quick-start-guide"}}},{"node":{"fields":{"slug":"/testing/manual-testing/"},"frontmatter":{"title":"Manual Testing","id":"manual-testing"}}}]},"markdownRemark":{"html":"<p>Wingsuit is a design system for twig based UI components. The components are developed independently in <a href=\"https://storybook.js.org/\">Storybook</a> and can be used by other “apps”, like Drupal. </p>\n<p>A Wingsuit component is defined by an wingsuit.yml, which is an extended variant of the <a href=\"https://ui-patterns.readthedocs.io/en/8.x-1.x/content/patterns-definition.html\">ui_patterns.yml</a>. </p>\n<p><b>Wingsuit uses this configuration file to:</b></p>\n<ul>\n<li>\n<p><b>Generate Storybook stories</b><br>\nEach variant is a Storybook story. Fields and settings are editable with <a href=\"https://github.com/storybookjs/storybook/tree/master/addons/knobs\">knobs</a>.</p>\n<h1 id=\"screenshot\"><a href=\"#screenshot\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>SCREENSHOT</h1>\n</li>\n<li>\n<p><b>Automatic Storybook documentation</b><br>\nA nice documentation page is created directly from the wingsuit.yml. </p>\n<h1 id=\"screenshot-1\"><a href=\"#screenshot-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>SCREENSHOT</h1>\n</li>\n<li>\n<p><b>Drupal integration</b><br>\nWith the <a href=\"https://www.drupal.org/project/wingsuit_companion\">wingsuit_companion</a> module, patterns can be used with zero configuration in drupal. </p>\n<h1 id=\"screenshot-2\"><a href=\"#screenshot-2\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>SCREENSHOT</h1>\n</li>\n</ul>\n<ul>\n<li>\n<p><b>Stay in sync</b><br>\nOne big challenge for design systems is to stay in sync between the apps.\nWingsuit solves this problem with a common component configuration - the <b>wingsuit.yml</b>.</p>\n</li>\n<li>\n<p><b>Frontend frameworks</b><br>\nWingsuit ships with <a href=\"https://tailwindcss.com/\">Tailwind CSS</a> and <a href=\"https://github.com/alpinejs/alpine\">Alpine.js</a>.\nBoth can easily be replaced with other frameworks like <a href=\"https://getbootstrap.com\">Bootstrap 4</a> or <a href=\"https://get.foundation/sites/docs/\">Foundation 6</a>.</p>\n</li>\n</ul>","fields":{"slug":"/basics/introduction/"},"frontmatter":{"title":"Introduction","id":"introduction"}}},"pathContext":{"slug":"/basics/introduction/"}}

